city can be produced for lower costs per MWh than fossil-based electricity. As a result of this difference in cost structure between fossil and renewable and nuclear technologies, the cost structure of the power sector will change during the transition. The sector will require more capital investment (CAPEX) and becomes less exposed to volatility and other changes in fuel prices, which as part of the operational expenditures (OPEX). The risk structure of the overall power sector changes in line with this development, from capital and fuel-related risks towards almost purely capital-related risks. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"text-wrapper"},[_c('h2',{staticClass:"chapter-title",attrs:{"id":"costs"}},[_vm._v(" Fuel Cost Risk ")]),_c('p',{staticClass:"cost-risk-paragraph"},[_vm._v(" One of the risks that we observe in the power sector is the fuel cost risk. Fuel cost risks include all potential changes to fuel costs, either due to market developments or as result of the introduction of a carbon price. As such, the indicator shown here includes both the Direct Emission Costs indicator as well as the Indirect Emission Cost indicator from the "),_c('a',{attrs:{"id":"link","href":"https://www.unepfi.org/wordpress/wp-content/uploads/2018/04/EXTENDING-OUR-HORIZONS.pdf","target":"_blank"}},[_vm._v("UNEP-FI RFPs")]),_vm._v(". With the introduction of a carbon price, fuel costs per MWh will rise, increasing the costs of fossil-based electricity production and driving a transition in the power sector from carbon-emitting generation to low-carbon electricity. Therefore investors face the risk that fossil-based assets, such as coal or gas-fired power plants without CCS lose their value under climate policy. This shift is reflected in the fuel costs changes depicted in the figure below. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass: